<template>
	<view class="addPoem">

		<view class="form">
			<u--form labelPosition="left" :model="modelForm" :rules="rules" ref="uForm">
				<u-form-item label="标题" prop="poem.title" borderBottom ref="title">
					<u--input placeholder="请输入" v-model="modelForm.poem.title" border="none"></u--input>
				</u-form-item>
				<u-form-item label="作者" prop="poem.author" borderBottom ref="author">
					<u--input placeholder="请输入" v-model="modelForm.poem.author" border="none"></u--input>
				</u-form-item>
				<u-form-item label="内容" prop="poem.poemWord" borderBottom ref="poemWord">
					<u--textarea border="none" autoHeight v-model="modelForm.poem.poemWord"
						placeholder="请输入内容，需要填写完整的标点符号"></u--textarea>
				</u-form-item>
			</u--form>
			<view class="btns">
				<u-button class="btn" plain type="error" text="重置" customStyle="" @click="reset"></u-button>
				<u-button class="btn" plain type="primary" @click="submit">提交</u-button>
			</view>
		</view>
		<view class="list">
			<u-cell-group>
				<u-cell url="/pages/addPoem/index" clickable title="唐诗" value="修改" :isLink="true"></u-cell>
			</u-cell-group>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				showSex: false,
				modelForm: {
					poem: {
						title: '',
						author: '',
						poemWord: '',
					},
				},
				rules: {
					'poem.title': {
						type: 'string',
						required: true,
						message: '请输入',
						trigger: ['blur', 'change']
					},
					'poem.author': {
						type: 'string',
						required: true,
						message: '请输入',
						trigger: ['blur', 'change']
					},
					'poem.poemWord': {
						type: 'string',
						required: true,
						message: '请输入',
						trigger: ['blur', 'change']
					},
				},
			}
		},
		onReady() {
			//如果需要兼容微信小程序，并且校验规则中含有方法等，只能通过setRules方法设置规则。
			this.$refs.uForm.setRules(this.rules)
		},
		methods: {
			reset() {
				const validateList = ['poem.name', 'poem.sex', 'radiovalue1', 'checkboxValue1', 'intro',
					'hotel', 'code', 'poem.birthday'
				]
				this.$refs.form1.resetFields()
				this.$refs.form1.clearValidate()
				setTimeout(() => {
					this.$refs.form1.clearValidate(validateList)
					// 或者使用 this.$refs.form1.clearValidate()
				}, 10)
			},
			submit() {
				this.$refs.uForm.validate().then(res => {
					uni.$u.toast('校验通过')
				}).catch(errors => {
					uni.$u.toast('校验失败')
				})
			}
		},
	}
</script>

<style lang="scss" scoped>
	.addPoem {
		padding: 20rpx;

		.form {
			padding: 20rpx;
			background-color: #fff;
			border-radius: 10rpx;
			box-shadow: #555 0px 1px 2px 0px;

			.btns {
				display: flex;
				align-items: center;
				margin-top: 40rpx;
				margin-bottom: 40rpx;

				.btn {
					width: 250rpx;
				}
			}
		}

		.list {
			margin-top: 40rpx;
			background-color: #fff;
			border-radius: 10rpx;
			box-shadow: #555 0px 1px 2px 0px;
		}
	}
</style>